import React from "react";
import P from './styled-components/P';

function Distance(props) {
    let kmText = "";
    let metrsText = "";

    function showDistance(i , isKm) {    
        if ((i % 100) >= 10 && (i % 100) <=20) {
            return i +  (isKm ? " ÐºÐ¸Ð»Ð¾Ð¼ÐµÑ‚Ñ€Ð¾Ð² " : " Ð¼ÐµÑ‚Ñ€Ð¾Ð²");
        }
        if (i >= 10 && i <=  20) {
            return i +  (isKm ? " ÐºÐ¸Ð»Ð¾Ð¼ÐµÑ‚Ñ€Ð¾Ð² " : " Ð¼ÐµÑ‚Ñ€Ð¾Ð²");
        }else {
            switch (i % 10) {
            case 0:
                if(i === 0){
                    return "";
                }else {
                    return i + (isKm ? " ÐºÐ¸Ð»Ð¾Ð¼ÐµÑ‚Ñ€Ð¾Ð² " : " Ð¼ÐµÑ‚Ñ€Ð¾Ð²");
                }
            case 1:
                return i + (isKm ? " ÐºÐ¸Ð»Ð¾Ð¼ÐµÑ‚Ñ€ " : " Ð¼ÐµÑ‚Ñ€");
            case 2:
            case 3:
            case 4:
                return i + (isKm ? " ÐºÐ¸Ð»Ð¾Ð¼ÐµÑ‚Ñ€Ð° " :" Ð¼ÐµÑ‚Ñ€Ð°");           
             default:
                return i + (isKm ? " ÐºÐ¸Ð»Ð¾Ð¼ÐµÑ‚Ñ€Ð¾Ð² " : " Ð¼ÐµÑ‚Ñ€Ð¾Ð²");
    
            }
        }};
    
    function calculateDistance(dist) {
        let metrs = dist % 1000;
        let km = (dist - metrs) / 1000;
        
        kmText = showDistance(km, true);
        metrsText = showDistance(metrs, false);
        
        };

    calculateDistance(props.distance)

    return(
        <P maxWidth={ props.maxWidth } fontSize={ props.fontSize } color={ props.color} fontHeight={ props.fontHeight }>
            { kmText && kmText}{ metrsText && metrsText }
        </P>
)};

export default Distance;